export function fetchProductsByFilters(filter, sort, pagination, admin) {
  let queryString = '';

  // --- CHANGES START HERE ---

  // Handle Filters
  for (let key in filter) {
    const filterValues = filter[key]; // categoryValues is now filterValues, can be for any filter (category, brand, etc.)
    if (filterValues.length > 0) {
      // For each selected value in the array, append it to the query string
      filterValues.forEach(value => {
        queryString += `${key}=${value}&`; // This will create: category=furniture&category=fragrance&
      });
    }
  }

  // --- CHANGES END HERE ---

  // Handle Sort
  for (let key in sort) {
    queryString += `${key}=${sort[key]}&`;
  }

  // Handle Pagination
  console.log(pagination);
  for (let key in pagination) {
    queryString += `${key}=${pagination[key]}&`;
  }

  // Handle Admin (if applicable)
  if (admin) {
    queryString += `admin:true`;
  }

  return new Promise(async (resolve) => {
    // TODO: we will not hard-code server URL here
    const response = await fetch('/products?' + queryString);
    const data = await response.json();
    const totalItems = await response.headers.get('X-Total-Count');
    resolve({ data: { products: data, totalItems: +totalItems } });
  });
}

