import './App.css';
import {
  createBrowserRouter,
  RouterProvider,
  Route,
  Link,
} from 'react-router-dom';
import CartPage from './features/Pages/CartPage';
import LoginPage from './features/Pages/LoginPage';
import Home from './features/Pages/Home';
import SignUpPage from './features/Pages/SignUpPage'
import Checkout from './features/Pages/Checkout';
import ProductDetailPage from './features/Pages/ProductDetailPage';
import Protected from './features/auth/Components/Protected';
import { useDispatch, useSelector } from 'react-redux';
import { checkAuthAsync, selectLoggedInUser, selectUserChecked } from './features/auth/authSlice';
import { useEffect } from 'react';
import { fetchItemsByUserIdAsync } from './features/cart/cartSlice';
import PageNotFound from './features/Pages/404'
import OrderSuccessPage from './features/Pages/OrderSuccessPage';
import UserOrders from './features/user/components/UserOrders';
import UserOrderPage from './features/Pages/UserOrderPage';
import UserProfile from './features/user/components/UserProfile';
import UserProfilePage from './features/Pages/UserProfilePage';
import { fetchLoggedInUserAsync, selectUserInfo } from './features/user/userSlice';
import Logout from './features/auth/Components/Logout';
import ForgotPasswordPage from './features/Pages/ForgotPasswordPage';
import ProtectedAdmin from './features/auth/Components/ProtectedAdmin';
import AdminHome from './features/Pages/AdminHome';
import AdminProductDetailPage from './features/Pages/AdminProductDetailPage';
import ProductForm from './features/admin/Components/ProductForm';
import AdminProductFormPage from './features/Pages/AdminProductFormPage';
import AdminOrder from './features/admin/Components/AdminOrder';
import AdminOrderPage from './features/Pages/AdminOrderPage';
import { checkAuth } from './features/auth/authAPI';
import StripeCheckout from './features/Pages/StripeCheckout';
import ResetPasswordPage from './features/Pages/ResetPasswordPage';

const router = createBrowserRouter([
  {
    path: '/',
    element: (
    
      <Protected>
        <Home></Home>
      </Protected>
    ),
  },
  {
    path: '/admin',
    element: (
    
      <ProtectedAdmin>
        <AdminHome></AdminHome>
      </ProtectedAdmin>
    ),
  },
  {
    path: '/login',
    element: <LoginPage></LoginPage>,
  },
  {
    path: '/signup',
    element: <SignUpPage ></SignUpPage>,
  },
  {
    path: '/cart',
    element: (
     
       <Protected>
         <CartPage></CartPage>
       </Protected>
    ),
  },
  {
    path: '/checkout',
    element: (
      <Protected>
        <Checkout></Checkout>
      </Protected>
    ),
  },
  {
    path: '/stripe-checkout/',
    element: (
      <Protected>
        <StripeCheckout></StripeCheckout>
      </Protected>
    ),
  },
  {
    path: '/product-detail/:id',
    element: (
      <Protected>
        <ProductDetailPage></ProductDetailPage>
      </Protected>
    ),
  },
  {
    path: '/admin/product-form',
    element: (
      <ProtectedAdmin>
        <AdminProductFormPage></AdminProductFormPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: '/admin/product-form/edit/:id',
    element: (
      <ProtectedAdmin>
        <AdminProductFormPage></AdminProductFormPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: '/admin/orders',
    element: (
      <ProtectedAdmin>
        <AdminOrderPage></AdminOrderPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: '/admin/product-detail/:id',
    element: (
      <ProtectedAdmin>
        <AdminProductDetailPage></AdminProductDetailPage>
      </ProtectedAdmin>
    ),
  },
  {
    path: '/order-success/:id',
    element: (
      <OrderSuccessPage></OrderSuccessPage>
    ),
  },
  {
    path: '/my-orders',
    element: (
      <UserOrderPage />
    ),
  },
  {
    path: '/profile',
    element: (
      <UserProfilePage />
    ),
  },
  {
    path: '/logout',
    element: (
      <Logout />
    ),
  },
  {
    path: '/forgot-password',
    element: (
      <ForgotPasswordPage />
    ),
  },
  {
    path: '/reset-password',
    element: (
      <ResetPasswordPage />
    ),
  },
  {
    path: '*',
    element: (
      <PageNotFound></PageNotFound>
    ),
  },
]);

function App() {
  const dispatch = useDispatch();
  const user = useSelector(selectLoggedInUser);
  const userChecked = useSelector(selectUserChecked)

  useEffect(()=>{
    dispatch(checkAuthAsync())
  },[])

  useEffect(()=>{
    if(user){
      dispatch(fetchItemsByUserIdAsync())
      dispatch(fetchLoggedInUserAsync())
    }
  },[dispatch, user])
  return (
    <div className="App">
     { userChecked && <RouterProvider router={router} /> }
    </div>
  );
}
export default App;